@import url(https://fonts.googleapis.com/css?family=Homemade+Apple);
@import url(https://fonts.googleapis.com/css?family=Open+Sans);
header {
  position: fixed;
  z-index: 999;
  width: 100%;
  white-space: nowrap;
  background-color: #668aff;
  max-height: 160px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  font-size: calc(10px + 2vmin);
  color: white;
  box-shadow: 0px 1px 1px #282c34;
}

#brand {
  font-family: "Homemade Apple", cursive;
  font-size: 40px;
}

#scoreboard {
  text-align: center;
}

@media screen and (max-width: 768px) {
  header {
    -webkit-flex-direction: column;
            flex-direction: column;
  }

  #brand {
    margin-top: 2px;
    margin-bottom: 0px;
    font-size: 35px
  }

  #scoreboard > h4, h5 {
    margin-top: 0px;
    margin-bottom: 10px;
  }
}

#board {
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 175px 40px 16px 40px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
}
.card {
    max-width: 100%;
    height: auto;
    margin: 1rem;
    background: #fff;
    border-radius: 5px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
}

.card:hover {
    cursor: pointer;
}

/* .card:active {

